.resume-main {
  background-color: var(--secondary-colour);
  flex-direction: column;
  padding: 4em 2em;
  section {
    display: flex;
    flex-direction: column;
    gap: 2em;
    margin-top: 2em;
  }
}

.resume-card {
  display: grid;
  grid-template-columns: .8fr 1.2fr;
  gap: 2em;
  padding: 4em 2em;
  margin-bottom: 4em;
}

.resume-title {
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.resume-description {
  display: flex;
  flex-direction: column;
  gap: 1em;
}

@media only screen and (min-width: 900px) {
  .resume-main section {
    width: clamp(700px, 40% + 5vw, 1000px);
  }
}

@media only screen and (max-width: 700px) {
  .resume-card {
    display: flex;
    flex-direction: column;
  }
}